@import "../variables";

.footer {
    position: fixed;
    z-index: 200;
    bottom: 0;
    left: 0;
    right: 0;
    min-width: 320px; }

.basket-btn {
    position: absolute;
    bottom: 10px;
    z-index: 200;
    left: 50%;
    transform: translateX(-50%);
    transition: all 0.3s ease;
    &__count {
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        top: -7px;
        right: -13px;
        width: 24px;
        height: 24px;
        padding: 2px;
        color: #ffffff;
        border-radius: 100%;
        background-color: #000000;
        opacity: 0.9; } }
.is-hide {
    display: none; }
