@import "variables";
@include font('Stratos-Regular', 'stratos-regular');
@include font('Stratos-Medium', 'stratos-medium');
@include font('Stratos-Bold', 'stratos-bold');

@mixin fontSR {
  font-family: 'Stratos-Regular', sans-serif;
  font-style: normal;
  font-weight: 400; }

@mixin fontSM {
  font-family: 'Stratos-Medium', sans-serif;
  font-style: normal;
  font-weight: 600; }
@mixin fontSB {
  font-family: 'Stratos-Bold', sans-serif;
  font-style: normal;
  font-weight: 700; }
::placeholder {
    @include fontSR; }
body {
  @include fontSR;
  width: 100vw;
  min-width: 320px; }
.out {
  width: 100%; }

.wrapper {
  width: 100%;
  min-width: 320px;
  padding: $basic-padding; }

h1 {
  @include fontSB; }

button {
  background-color: transparent; }

a {
  text-decoration: none; }

.padding {
  height: 75px;
  margin-top: -20px;
  background-color: $basic-background-color; }

* {
  outline: none;
  -ms-touch-action: manipulation;
  touch-action: manipulation; }


.photos {

	&__title {
		@include fontSM;
		font-size: 30px;
		text-align: center;

		margin-bottom: 50px;

		a {
			color:#c2a978 {} } }

	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between; }

	&__wrap {
		display: flex;
		flex-direction: column; }

	&__wrap &__item {
		width: 175px;
		height: 175px; }


	&__item {
		width: 380px;
		height: 380px;
		margin-bottom: 30px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } } }
