.tabs {
  overflow: auto;
  outline: none;
  z-index: 200;
}

.tabs__list {
  margin: 0;
  padding: 8px 16px;
  white-space: nowrap;
  text-align: center;
}

.tabs__tab {
  display: inline-block;
  position: relative;
  cursor: pointer;
}

.tabs__tab:not(:first-child) {
  margin-left: 8px;
}

.tabs__tab.active .tabs__description {
  color: black;
}

.tabs__marker {
  position: absolute;
  z-index: -1;
  top: 10px;
  width: 64px;
  height: 64px;
  border-radius: 25%;
  background-color: #F9D953 !important;
  transition: transform 0.5s, width 0.5s, background-color 0.5s;
}

.tabs__img-box {
  position: relative;
  width: 64px;
  height: 64px;
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
  font-size: 38px;
  margin: 0 auto;
  margin-bottom: 4px;
}

.tabs__img {
  transition: all 0.4s ease-in-out;
}

.tabs__img:focus {
  transform: scale(1.1);
}

.tabs__description {
  display: flex;
  justify-content: center;
  max-width: 72px;
  text-align: center;
  line-height: 16px;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.5);
