.contacts__icon {
  position: absolute;
  left: 32px;
  top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 26px;
  width: 26px;
  margin-right: 12px;
  border-radius: 50%;
}

.contacts__icon svg {
  font-size: 28px;
  fill: linear-gradient(to top left, #000000 0%, #ffffff 100%);
}

.social__link {
  color: #000;
