}

.social__link:hover .social__icon {
  opacity: 0.7;
}

.social__title {
  font-family: 'Stratos-Regular', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-weight: 600;
  font-size: 17px;
}

.social__icon {
  position: absolute;
  left: 32px;
  top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 24px;
  width: 24px;
  margin-right: 12px;
  border-radius: 50%;
  transition: all 0.2s linear;
}

.social__icon svg {
  fill: #ffffff;
