  transform: translateY(100%);
}

.basket__content {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 100;
  height: calc(var(--app-height) - 120px);
  overflow: auto;
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  background-color: #fff;
  box-shadow: 0 -4px 8px rgba(0, 0, 0, 0.1);
