@import "./variables";
.basket {
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    top: 0;
    transition: transform 0.3s ease-in-out;
    transform: translateY(100%);
    &__content {
        position: absolute;
        bottom: 0;
        right: 0;
        left: 0;
        z-index: 100;
        height: calc(var(--app-height) - 120px);
        overflow: auto;
        border-top-left-radius: 25px;
        border-top-right-radius: 25px;
        background-color: #fff;
        box-shadow: 0 -4px 8px rgba(0, 0, 0, 0.1); }
    &__overlay {
        position: fixed;
        display: none;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 90;
        width: 100vw;
        min-width: 320px;
        height: 100vh;
        background-color: rgba(0,0,0,.5); }
    &__total-amount {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 10px;
        margin: 15px 0;
        font-size: $medium-font-size; }
    &__close {
        position: absolute;
        top: 0;
        left: 0;
        padding: 15px;
        font-size: 30px; }

    // .purchases__header
    &__header {
        display: flex;
        align-items: center;

        height: 60px;
        border-top-left-radius: 25px;
        border-top-right-radius: 25px;
        background-color: #fff;
        margin-bottom: 20px; }
    &__info {
        margin-left: auto;
        margin-right: 5px;
        font-size: $medium-font-size; }
    &__quantity {
        margin-right: auto;
        span {
            padding: 9px;
            font-size: $medium-font-size; } }
    &.is-shown {
        .basket__overlay {
            display: block; } } }

.icon-minus {
    width: 14px; }
.is-shown {
    transform: translateY(0); }
